import {useRef, useEffect} from 'react';
import ona from './1.png'
import twona from './2.png'
import threna from './3.png'
import foura from './4.png'
import fiva from './5.png'
import sixa from './6.png'
import $ from 'jquery'; 
function Cards() {
  var body = document.body,
  html = document.documentElement;

var heightt = Math.max( body.scrollHeight, body.offsetHeight, 
                     html.clientHeight, html.scrollHeight, html.offsetHeight );
  console.log(heightt);
    
     

    let  showClick = e=> {
        let elem= document.getElementById('thetarget')
        let imgpath=   e.currentTarget.getAttribute("thesource");
        let thename=e.currentTarget.getAttribute("thename");
      document.getElementById("nowucanshowme").classList.remove('thehidden-div');
      document.getElementById("nowucanshowme").classList.add('theshow-div');
      document.getElementById("yyy").classList.remove("thehidden-div");
      document.getElementById("yyy").classList.add("theinnershow-div");
        // elem.classList.add('theshow-div');
        //elem.classList.remove('thehidden-div');
        document.getElementById("thename").innerHTML=thename;
        elem.setAttribute('src',imgpath);

        var body = document.body,
        html = document.documentElement;
    
    var height = Math.max( body.scrollHeight, body.offsetHeight, 
                           html.clientHeight, html.scrollHeight, html.offsetHeight );
        document.getElementById("nowucanshowme").style.height=height+"px";
     

        console.log(imgpath);
    
    }

let hideclick=_=>{
  document.getElementById("nowucanshowme").classList.add('thehidden-div');
  document.getElementById("nowucanshowme").classList.remove('theshow-div');
  document.getElementById("yyy").classList.add("thehidden-div");
  document.getElementById("yyy").classList.remove("theinnershow-div");

  
        document.getElementById("nowucanshowme").style.height="0px";



}

   
   
   

  return (
   <>


<div>

<h2 className="text-center tobold " >PORTFOLIO</h2>
<div className="text-center position-relative">
<h2 className=" thestar3 fs-3"><i class="fa-solid fa-star fs-1 ex"></i></h2>
</div>
</div>
   <div className='w-60'>
<div className='container py-5 h-auto'>
<div className='row'>

<div className='col-md-4 theconofimg  gy-3 '>

<div className='radiudiv'>
<img src={ona} alt="" />
<div className='hs' thename='LOG CABIN' thesource={ona} onClick={showClick}>
<div className='pluscov'><i class="fa-solid fa-plus text-white fs-64"></i></div>
</div>
</div>
</div>

<div className='col-md-4 theconofimg gy-3  '>

<div className='radiudiv'>
<img src={twona} alt="" />
<div className='hs' thename='TASTY CAKE' thesource={twona} onClick={showClick}>
<div className='pluscov'><i class="fa-solid fa-plus text-white fs-64"></i></div>
</div>
</div>
</div>




<div className='col-md-4 theconofimg gy-3 '>

<div className='radiudiv'>
<img src={threna} alt="" />
<div className='hs' thename='CIRCUS TENT' thesource={threna} onClick={showClick}>
<div className='pluscov'><i class="fa-solid fa-plus text-white fs-64"></i></div>
</div>
</div>
</div>



<div className='col-md-4 theconofimg gy-3  '>

<div className='radiudiv'>
<img src={foura} alt="" />
<div className='hs' thename='CONTROLLER' thesource={foura} onClick={showClick}>
<div className='pluscov'><i class="fa-solid fa-plus text-white fs-64"></i></div>
</div>
</div>
</div>


<div className='col-md-4 theconofimg gy-3  '>

<div className='radiudiv'>
<img src={fiva} alt="" />
<div className='hs' thename='LOCKED SAFE' thesource={fiva} onClick={showClick}>
<div className='pluscov'><i class="fa-solid fa-plus text-white fs-64"></i></div>
</div>
</div>
</div>


<div className='col-md-4 theconofimg gy-3  '>

<div className='radiudiv'>
<img src={sixa} alt="" />
<div className='hs' thename='SUBMARINE' thesource={sixa} onClick={showClick}>
<div className='pluscov'><i class="fa-solid fa-plus text-white fs-64"></i></div>
</div>
</div>
</div>



</div>





</div>
</div>
<div>
  
</div>
<div className='thehidden-div p-5 d-flex justify-content-center' id='nowucanshowme'>
<div className='bg-white w-100 m-auto rounded h-auto theinnerhidden-div' id='yyy'>

    <div className=''><i onClick={hideclick}  class="fa-solid fa-xmark greeclose fs-111"></i></div>
    <div>

    <h2 className="text-dark text-center fs-111 " id='thename'>NOW</h2>
    <div className="containerofstar">
<h2 className="text-dark thestar2 fs-3"><i class="fa-solid fa-star fs-111"></i></h2>
</div>
    </div>
    <div className='text-center w-50 m-auto'>  <img src="" alt="" id='thetarget' className='iminpop' /></div>
    <div className='p-5 text-center w-50 m-auto'><p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Mollitia neque assumenda ipsam nihil, molestias magnam, recusandae quos quis inventore quisquam velit asperiores, vitae? Reprehenderit soluta, eos quod consequuntur itaque. Nam.</p></div>
    <div className='text-center'><button onClick={hideclick} className='greebut fs-5'><i class="fa-solid fa-xmark p-2  "></i>close window</button></div>
    
</div>
</div>
   </>
  );
}

export default Cards;